import React from 'react'
import { useState } from 'react'
import { writeSlider } from '../../../../helpers/db'
import { ItemCompotent } from './../itemComponent/ItemCompotent'

export const SettingsSlider = () => {
  const [listSettings, setListSettings] = useState<any>([])

  const handleClick = (e: any) => {
    setListSettings((state: any) => [
      ...state,
      {
        image: '',
        date: new Date(),
      },
    ])
  }

  const handleSubmit = async (event: any) => {
    console.log('listSettings', listSettings)

    event.preventDefault()
    writeSlider(event)
    // setError('');
    // try {
    //   await signup(email, password);
    // } catch (error) {
    //   setError(error.message);
    // }
  }

  return (
    <div>
      <form
        // className={`mt-5 py-5 px-5 `}
        autoComplete='off'
        onSubmit={handleSubmit}
      >
        {listSettings.map((e: any, i: number) => {
          return <ItemCompotent key={i} data={e} id={i} />
        })}

        <button className='btn btn-danger mr-2' type='submit'>
          Save
        </button>
      </form>
      <button type='button' onClick={handleClick}>
        {' '}
        Создать New слайдер
      </button>
    </div>
  )
}
