import React, { useState } from 'react'
import DatePicker from 'react-datepicker'
import { useEffect } from 'react'

export const EndTime = ({ handleChangeDate, data }: any) => {
  const [startDate, setStartDate] = useState<any>(data)

  useEffect(() => {
    handleChangeDate(startDate)
  }, [startDate])

  return (
    <DatePicker
      selected={startDate}
      name='time'
      onChange={(date: any) => setStartDate(date)}
      showTimeSelect
      showTimeSelectOnly
      timeIntervals={10}
      timeCaption='Time'
      dateFormat='h:mm aa'
    />
  )
}
